// Футер

footer{
	width: 100%;
	background-color: #f2f5f9;
	@include museo300;
	color: $content;	
	font-size: 15px;
	letter-spacing: 0.24px;
	li{
		@include museo500;	
		font-size: 15px;
		margin-bottom: 18px;
		@include max-screen (321px) {
			margin-bottom: 10px;
			text-align: center;
		}
	}	
	a{
		color: $content;
		transition: all 0.3s linear;	
		&:hover, &:focus{
			color: $func_color;
		}
	}
	.footer-content {
		padding: 70px 0;
		@include flex($j-sb,$a-fs);
		@include max-screen (768px) {
			flex-wrap: wrap;
			padding-bottom: 40px;
		}
		@include max-screen (426px) {
			//flex-direction: column;
			//align-items: center;	
			justify-content: center;
		}
		@include max-screen (321px) {
			padding-top: 40px;
		}
		.footer-logo-block {
			width: 200px;
			height: 45px;
			margin-top: -5px;
			@include max-screen (426px) {
				width: 100%;
			}
			img{
				margin-bottom: 25px;
				width: 100%;
				height: 100%;
				margin-left: -20px;
				@include max-screen (768px) {
					max-width: 180px;
					margin-left: 0;
				}
				@include max-screen (426px) {
					// flex-basis: 100%;
					max-width: 400px;
					width: 100%;
					// object-fit: cover;
				}
			}
			a.footer-mail{
				max-width: 200px;
				display: block;
				margin-bottom: 20px;
				@include max-screen (426px) {
					margin-bottom: 0;
					max-width: 100%;
					text-align: center;
				}

			}
			.footer-icon-block {
				i::before{
					font-size: 30px;
					color: #929aa8;
					transition: all 0.3s linear;	
					margin-right: 20px;
				}
				i:hover::before, &:focus::before {
						color: $func_color;
					}
				@include max-screen (426px) {
					@include flex($j-cc,$a-ic);	
					padding-top: 20px;
				}
			}
			@include max-screen (768px) {
				order: 3;
				flex-basis: 50%;
			}
			@include max-screen (426px) {
				order: 5;
				// @include flex($j-sb,$a-ic);	
				margin-top: 20px;
				flex-basis: 100%;
				margin-bottom: 100px;

			}
			@include max-screen (376px) {
				justify-content: center;
			}
		}
		.footer-nav-block {
			flex-basis: 40%;
			@include max-screen (992px) {
				flex-basis: 25%;
			}
			@include max-screen (768px) {
				flex-basis: 100%;
				margin-bottom: 30px;
			}
			@include max-screen (321px) {
				margin-bottom: 0;
				flex-basis: 60%;
			}
			.foot-list {
				padding-top: 5px;
				max-height: 170px;
				@include flex($j-sb,$a-fs,$fww);
				flex-direction: column;
				@include max-screen (992px) {
					padding-top: 0;
					max-height: 190px;
				}
				@include max-screen (500px) {
					max-height: 100%;
					align-items: center;	
				}
				li{
					margin-right: 170px;
					@include max-screen (1170px) {
						margin-right: 80px;
					}
					@include max-screen (992px) {
						margin-right: 0;
						margin-bottom: 5px;
					}
					@include max-screen (768px) {
						margin-bottom: 18px;
						margin-right: 210px;
					}
					@include max-screen (500px) {
						margin-right: 0;
					}
				}	
			}
		}
		.footer-contact-block {
			width: 270px;
			height: 140px;
			padding: 35px;
			border: 1px dashed #88c5ef;
			border-radius: 10px;
			@include flex;
			flex-direction: column;
			@include max-screen (992px) {
				padding: 20px;
			}
			@include max-screen (768px) {
				order: 4;
				flex-basis: 50%;
			}
			@include max-screen (500px) {
				padding: 10px;
			}
			@include max-screen (426px) {
				flex-basis: 100%;
				padding: 25px;
			}
			p{
				text-align: center;
				@include max-screen (426px) {
					max-width: 210px;
				}
			}
			.big-number {
				@include museo700;
				font-size: 24px;
				color: #88c5ef;
				margin-bottom: 10px;	
				&:hover, &:focus{
					color: $content;	
				}
				@include max-screen (500px) {
					font-size: 20px;
				}
			}
		}
	}
	hr{
		margin: 0;
		height: 1px;
		border: 0px none;
		color: #dee1e5;
		background-color: #dee1e5;
	}
	.footer-prava {
		@include flex($j-sb,$a-ic);
		padding: 20px 0;
		font-size: 13px;
		@include max-screen (768px) {
			text-align: center;
		}
		@include max-screen (426px) {
			flex-wrap: wrap;
			a{
				flex-basis: 40%;
				text-align: left;
				margin-bottom: 10px;
			}
		}
		span{
			&:hover, &:focus{
				opacity: .75;
			}
		}
	}
}
